import * as React from 'react'
import { Modal, Form, Input, Button, message } from 'antd'

import { FormComponentProps } from 'antd/lib/form'
import Intl from '@components/IntlTelInput'
import * as styles from './style.scss'
import { observable, action } from 'mobx'
import { observer } from 'mobx-react'
import { usrRegister, IusrRegister, getCode } from '@src/api/homePage'
import homePage from '@src/stores/homePage'

const FormItem = Form.Item
interface IJoinModal extends FormComponentProps {
  visible: boolean
  closeCallBack: () => void
}
let timer: number
@observer
class JoinModal extends React.Component<IJoinModal> {
  @observable timer: number = 60
  @observable intl: string = 'cn+86'
  @observable submited: boolean = false
  @observable loading:boolean = false
  get codeStr() {
    if (this.timer === 60) {
      return 'Get Verification Code'
    } else {
      return this.timer + 's'
    }
  }

  get canGetCode() {
    const phone = this.props.form.getFieldValue('phoneNumber')
    if (homePage.hasPhone) {
      return false
    }
    if (this.timer !== 60) {
      return false
    } else {
      if (this.intl !== 'cn+86') {
        if (phone.length > 10) {
          return true
        }
        return false
      } else {
        if (this.testPhone(phone)) {
          return true
        } else {
          return false
        }
      }
    }
  }

  @action
  intlOnchange = (value: string) => {
    this.intl = value
  }

  validateTeamMember = (rule: any, value: any, callback: any) => {
    const reg = new RegExp('^[0-9]*$')
    if (!reg.test(value)) {
      callback('teamMember must be numbers')
    }
    callback()
  }

  validateEmail = (rule: any, value: any, callback: any) => {
    if (!value) {
      callback('teamMember must be numbers')
    }
    const reg = new RegExp(
      '^[a-z0-9A-Z]+[- | a-z0-9A-Z . _]+@([a-z0-9A-Z]+(-[a-z0-9A-Z]+)?\\.)+[a-z]{2,}$'
    )
    if (!reg.test(value)) {
      callback('Incorrect email format')
    }
    callback()
  }

  teamNameBlur = async () => {
    const value = this.props.form.getFieldValue('teamName')
    if (value) {
      await homePage.judgeHasTeamName({ teamname: value })
      if (homePage.hasTeamName) {
        this.props.form.setFields({
          teamName: {
            value,
            errors: [new Error('Team Name has been registered')],
          },
        })
      }
    }
  }

  phoneBlur = async () => {
    const value = this.props.form.getFieldValue('phoneNumber')
    if (value) {
      await homePage.judgeHasPhone({ phone: value })
      if (homePage.hasPhone) {
        this.props.form.setFields({
          phoneNumber: {
            value,
            errors: [new Error('Phone has been registered')],
          },
        })
      }
    }
  }

  emailBlur = async () => {
    const value = this.props.form.getFieldValue('email')
    if (value) {
      await homePage.judgeHasEmail({ email: value })
      if (homePage.hasEmail) {
        this.props.form.setFields({
          email: {
            value,
            errors: [new Error('Email has been registered')],
          },
        })
      }
    }
  }

  testPhone = (phone: number) => {
    const reg = /^[1][3,4,5,7,8][0-9]{9}$/
    return phone && reg.test(phone.toString())
  }

  @action
  onOk = () => {
    if (this.submited) {
      this.props.closeCallBack()
      return
    }
    this.props.form.validateFields(async (err, data) => {
      if (!err) {
        const params: IusrRegister = {
          teamname: data.teamName,
          phonenum: data.phoneNumber,
          teammember: Number(data.teamMember),
          contractname: data.contractName,
          email: data.email,
          verifycode: data.verifyCode,
        }
        this.loading = true
        const res = await usrRegister(params)
        this.loading = false

        if (res && res.data) {
          if (!res.data.error) {
            this.submited = true
          } else {
            message.error(res.data.error)
          }
        }
      }
    })
  }

  @action
  getCode = async () => {
    if (homePage.hasPhone) {
      return 
    }
    const phone = this.props.form.getFieldValue('phoneNumber')
    const intl = this.intl.split('+')[1]
    const res = await getCode({ phonenum: phone,areacode: Number(intl)})
    if (res) {
      if (this.timer === 60) {
        timer = window.setInterval(() => {
          if (this.timer === 0) {
            clearInterval(timer)
            this.timer = 60
          } else {
            this.timer = this.timer - 1
          }
        }, 1000)
      }
    }
  }

  componentWillUnmount() {
    clearInterval(timer)
  }

  render() {
    const { getFieldDecorator } = this.props.form
    const { visible, closeCallBack } = this.props
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 7 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 },
      },
    }
    const footer = (
      <div className={styles.footer}>
        <Button loading={this.loading} onClick={this.onOk}>{this.submited ? 'close' : 'Join'}</Button>
      </div>
    )

    return (
      <Modal
        className={styles.joinModal}
        visible={visible}
        onCancel={closeCallBack}
        title={'Join Competition'}
        maskClosable={false}
        footer={footer}
      >
        {this.submited ? (
          <div className={styles.title}>
            Thank you for joining TSCC2050 Competition.
            <br />
            You can read the
            <a href=""> rules </a>
            for more information.
          </div>
        ) : (
          <>
            <div className={styles.title}>
              Welcome to join the TSC2050 Competition.
              <br />
              Pease complete the information about your team.
            </div>
            <Form>
              <FormItem {...formItemLayout} label="Team Name">
                {getFieldDecorator('teamName', {
                  rules: [
                    {
                      required: true,
                    },
                  ],
                })(<Input maxLength={75} onBlur={this.teamNameBlur} />)}
              </FormItem>
              <FormItem {...formItemLayout} label="Team member">
                {getFieldDecorator('teamMember', {
                  rules: [
                    {
                      required: true,
                      validator: this.validateTeamMember,
                    },
                  ],
                })(<Input maxLength={3} />)}
              </FormItem>
              <FormItem {...formItemLayout} label="Contract Name">
                {getFieldDecorator('contractName', {
                  rules: [
                    {
                      required: true,
                    },
                  ],
                })(<Input maxLength={75} />)}
              </FormItem>
              <FormItem
                {...formItemLayout}
                label="Phone Number"
                required={true}
              >
                <div className={styles.phoneNumber}>
                  <FormItem>
                    <Intl value={this.intl} onChange={this.intlOnchange} />
                  </FormItem>
                  <FormItem>
                    {getFieldDecorator('phoneNumber', {
                      rules: [
                        {
                          required: true,
                        },
                      ],
                    })(
                      <Input
                        maxLength={20}
                        style={{ width: '200px' }}
                        onBlur={this.phoneBlur}
                      />
                    )}
                  </FormItem>
                </div>
              </FormItem>
              <FormItem
                {...formItemLayout}
                label="Verification Code"
                required={true}
              >
                <div className={styles.verifyCode}>
                  <FormItem>
                    {getFieldDecorator('verifyCode', {
                      rules: [
                        {
                          required: true,
                        },
                      ],
                    })(<Input maxLength={10} style={{ width: '150px' }} />)}
                  </FormItem>
                  <FormItem>
                    <Button
                      className={this.canGetCode ? '' : 'disable'}
                      disabled={!this.canGetCode}
                      onClick={this.getCode}
                    >
                      {this.codeStr}
                    </Button>
                  </FormItem>
                </div>
              </FormItem>
              <FormItem {...formItemLayout} label="Email">
                {getFieldDecorator('email', {
                  rules: [
                    {
                      required: true,
                      validator: this.validateEmail,
                    },
                  ],
                })(<Input onBlur={this.emailBlur}/>)}
              </FormItem>
            </Form>
          </>
        )}
      </Modal>
    )
  }
}

export default Form.create()(JoinModal)



