import axios, { AxiosRequestConfig, AxiosResponse } from 'axios'
// import { message } from 'antd'

// import history from '@common/history'
// import { LOGIN } from '@common/path'

export enum Ret {
  正常 = 0,
  用户名不存在或者密码错误 = 99,
  登录失效 = 100,
  账号被冻结 = 101,
  账号重复 = 102,
  原始密码错误 = 103,
  参数错误 = 400,
  无权限 = 401,
  未找到错误 = 404,
  拒绝访问该资源 = 410,
  系统内部错误 = 1000,
  重启账号失败 = 2206,
  数据过期 = 4001,
  角色名重复 = 7100,
}

axios.interceptors.request.use(function(config: AxiosRequestConfig) {
  config.headers.post['content-type'] = 'application/json'
  if (process.env.API_URL) {
    config.baseURL = process.env.API_URL
  }
  return config
})

axios.interceptors.response.use(
  function(response: AxiosResponse) {
    return response
  },
  function(error: Error) {
    // Do something with response error
    return Promise.reject(error)
  }
)

export async function get<Data>(
  url: string,
  params?: {},
  config?: AxiosRequestConfig,
  fn?: () => void
) {
  if (params) {
    url = url + params2string(params)
  }
  return axios
    .get<ICommonResponse<Data>>(url, config)
    .then(handleResponse)
    .then(res => handleRetCallback(res, fn!))
    .catch(err => {
      // console.log(err)
    })
}

export async function post<Data>(
  url: string,
  params?: AxiosRequestConfig['data'],
  config?: AxiosRequestConfig,
  fn?: () => void
) {
  return axios
    .post<ICommonResponse<Data>>(url, params, config)
    .then(handleResponse)
    .then(res => handleRetCallback(res, fn!))
    .catch(err => {
      // console.log(err)
    })
}

function handleResponse(res: AxiosResponse) {
  // if (res.data.ret !== 0) {
  //   message.error(Ret[res.data.ret])
  //   if (res.data.ret === Ret.登录失效) {
  //     localStorage.clear()
  //     history.push(LOGIN)
  //   }
  //   return false
  // } else {
  //   return res.data
  // }
  return res
}

function handleRetCallback(res: AxiosResponse, fn: () => void) {
  if (!res) {
    if (fn) {
      fn()
    }
  }
  return res
}

export function params2string(params: {}) {
  let s = '?'
  Object.keys(params).map(
    key => params[key] !== undefined && (s += `${key}=${params[key]}&`)
  )
  return s.slice(0, -1)
}

export interface ICommonResponse<Data> {
  ret: number
  data: Data
  message: string
}

export function getQueryString(name: string) {
  const reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)')
  const r = window.location.search.substr(1).match(reg)
  if (r != null) {
    return r[2]
  }
  return ''
}



