import { get, post } from './utils'
const publicURL =
  process.env.REACT_APP_GITHUB === 'true'
    ? 'http://dev.trdsp.com/api/tscc2050'
    : 'http://172.18.32.151/api/tscc2050'

export function getCode(params: IgetCodeReq) {
  return get(publicURL + '/sendverifycode', params)
}
export interface IgetCodeReq {
  phonenum: string
  areacode: number
}

export function hasPhoneNum(params: IhasPhonenum) {
  return get(publicURL + '/hasphonenum', params)
}
export interface IhasPhonenum {
  phone: number
}

export function hasTeamName(params: IhasTeamName) {
  return get(publicURL + '/hasteamname', params)
}
export interface IhasTeamName {
  teamname: string
}

export function hasEmail(params: IhasEmail) {
  return get(publicURL + '/hasemail', params)
}

export interface IhasEmail {
  email: string
}

export function usrRegister(params: IusrRegister) {
  return post(publicURL + '/usrregister', params)
}
export interface IusrRegister {
  teamname: string
  phonenum: string
  email: string
  teammember: number
  contractname: string
  verifycode: string
}

export function uploadTask(params: IuploadTask) {
  return post(publicURL + '/commit', params)
}
export interface IuploadTask {
  teamname: string
  phonenum: string
  setname: string
  plantext: string
}

export function queryCommit() {
  return get<IqueryCommit[]>(publicURL + '/querycommit')
}
export interface IsetResult {
  setname: string
  score: number
  laststate: string
  committime: string
  lastresult: string
  commitnum: number
}
export interface IqueryCommit {
  teamname: string
  phonenum: string
  result: IsetResult[]
}



