import * as React from 'react'
import { Modal, Form, Input, Button, Select, message } from 'antd'

import { FormComponentProps } from 'antd/lib/form'
import Intl from '@components/IntlTelInput'
import * as styles from './style.scss'
import { observable, action } from 'mobx'
import { observer } from 'mobx-react'
import { IuploadTask, uploadTask } from '@src/api/homePage'
import homePage from '@src/stores/homePage'
const FormItem = Form.Item
interface IJoinModal extends FormComponentProps {
  visible: boolean
  closeCallBack: () => void
}
let timer: number
@observer
class JoinModal extends React.Component<IJoinModal> {
  @observable timer: number = 60
  @observable intl: string = 'cn+86'
  @observable submited: boolean = false
  @observable plantxt: string | null | ArrayBuffer
  get codeStr() {
    if (this.timer === 60) {
      return 'Get Verification Code'
    } else {
      return this.timer + 's'
    }
  }

  get canGetCode() {
    if (this.timer !== 60) {
      return false
    } else {
      if (this.intl !== 'cn+86') {
        return true
      } else {
        if (this.testPhone(this.props.form.getFieldValue('phoneNumber'))) {
          return true
        } else {
          return false
        }
      }
    }
  }

  @action
  intlOnchange = (value: string) => {
    this.intl = value
  }

  validateTeamName = (rule: any, value: any, callback: any) => {
    if (!value) {
      callback('请输入账号')
      return
    }
    if (value.indexOf(' ') > -1) {
      callback('请输入正确账号')
    }
    callback()
  }

  testPhone = (phone: number) => {
    const reg = /^[1][3,4,5,7,8][0-9]{9}$/
    return phone && reg.test(phone.toString())
  }
  onOk = () => {
    if (this.submited) {
      this.props.closeCallBack()
      return
    }
    this.props.form.validateFields(async (err, data) => {
      if (!err) {
        if(!this.plantxt) {
          message.error("Please upload the file.")
        }
        const params: IuploadTask = {
          teamname: data.teamName,
          phonenum: data.phoneNumber,
          setname: data.setName,
          plantext: this.plantxt as string,
        }
        const res = await uploadTask(params)
        if (res && res.data) {
          if (!res.data.error) {
            this.submited = true
          } else {
            message.error(res.data.error)
          }
        }
      }
    })
  }

  @action
  getCode = () => {
    if (this.timer === 60) {
      timer = window.setInterval(() => {
        if (this.timer === 0) {
          clearInterval(timer)
          this.timer = 60
        } else {
          this.timer = this.timer - 1
        }
      }, 1000)
    }
  }

  teamNameBlur = async () => {
    const value = this.props.form.getFieldValue('teamName')
    if (value) {
      await homePage.judgeHasTeamName({ teamname: value })
      if (!homePage.hasTeamName) {
        this.props.form.setFields({
          teamName: {
            value,
            errors: [new Error('Team Name has not yet been registered')],
          },
        })
      }
    }
  }

  phoneBlur = async () => {
    const value = this.props.form.getFieldValue('phoneNumber')
    if (value) {
      await homePage.judgeHasPhone({ phone: value })
      if (!homePage.hasPhone) {
        this.props.form.setFields({
          phoneNumber: {
            value,
            errors: [new Error('Phone has not yet been registered')],
          },
        })
      }
    }
  }

  componentWillUnmount() {
    clearInterval(timer)
  }

  @action
  fileChange = () => {
    const reads = new FileReader()
    // @ts-ignore
    const f = document.getElementById('file')!.files[0]
    reads.readAsText(f)
    const that = this
    reads.onload = function(e) {
      that.plantxt = this.result
    }
  }

  render() {
    const { getFieldDecorator } = this.props.form
    const { visible, closeCallBack } = this.props
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 7 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 },
      },
    }
    const footer = (
      <div className={styles.footer}>
        <Button onClick={this.onOk}>
          {this.submited ? 'close' : 'Submit'}
        </Button>
      </div>
    )
    const sets = [
      'hangzhou_kn_hz_1h_7_8_827',
      'hangzhou_bc_tyc_1h_2021',
      'hangzhou_bc_tyc_1h_7_8_1848',
      'hangzhou_bc_tyc_1h_8_9_2231',
      'hangzhou_sb_sx_1h_7_8_1671',
    ]
    return (
      <Modal
        className={styles.uploadModal}
        visible={visible}
        onCancel={closeCallBack}
        title={'Submission'}
        maskClosable={false}
        footer={footer}
      >
        {this.submited ? (
          <div className={styles.title}>
            Upload Success!
            <br />
            Remaining upload:{this.props.form.getFieldValue('setName')}
          </div>
        ) : (
          <>
            <div className={styles.title}>
              Uploading your team’s traffic siugnal plan for evaluation.
            </div>
            <Form>
              <FormItem {...formItemLayout} label="Team Name">
                {getFieldDecorator('teamName', {
                  rules: [
                    {
                      required: true,
                    },
                  ],
                })(<Input maxLength={75} onBlur={this.teamNameBlur} />)}
              </FormItem>
              <FormItem
                {...formItemLayout}
                label="Phone Number"
                required={true}
              >
                <div className={styles.phoneNumber}>
                  <FormItem>
                    <Intl value={this.intl} onChange={this.intlOnchange} />
                  </FormItem>
                  <FormItem>
                    {getFieldDecorator('phoneNumber', {
                      rules: [
                        {
                          required: true,
                        },
                      ],
                    })(
                      <Input
                        maxLength={20}
                        style={{ width: '200px' }}
                        onBlur={this.phoneBlur}
                      />
                    )}
                  </FormItem>
                </div>
              </FormItem>
              <FormItem {...formItemLayout} label="Training Set">
                {getFieldDecorator('setName', {
                  rules: [
                    {
                      required: true,
                    },
                  ]
                })(
                  <Select placeholder="Select a Training Set">
                    {sets.map((item: string) => {
                      return (
                        <Select.Option key={item} value={item}>
                          {item}
                        </Select.Option>
                      )
                    })}
                  </Select>
                )}
              </FormItem>
              <FormItem {...formItemLayout} label="Signal Plan" required={true}>
                <Button className={styles.uploadBtn}>Upload</Button>
                <input
                  type="file"
                  id="file"
                  name="file"
                  accept=".txt"
                  onChange={this.fileChange}
                  style={{ opacity: 0 }}
                />
              </FormItem>
            </Form>
          </>
        )}
      </Modal>
    )
  }
}

export default Form.create()(JoinModal)



