import * as React from 'react'
import bg from '@assets/homeImages/mobile/mobile_bg.png'
import trophy from '@assets/homeImages/mobile/mobile_trophy.png'
import Timeline1 from '@assets/homeImages/mobile/Timeline1.png'
import Timeline2 from '@assets/homeImages/mobile/Timeline2.png'
import Timeline3 from '@assets/homeImages/mobile/Timeline3.png'
import Timeline4 from '@assets/homeImages/mobile/Timeline4.png'
import Timeline5 from '@assets/homeImages/mobile/Timeline5.png'
import * as styles from './style.scss'
import * as moment from 'moment'
export default class MobileHome extends React.Component {
  getTimeLineImage = () => {
    if (moment(new Date()).isBefore('2019-03-15 00:00:00', 'day')) {
      return Timeline1
    } else if (
      moment(new Date()).isBetween('2019-03-15 00:00:00', '2019-03-25 00:00:00')
    ) {
      return Timeline2
    } else if (
      moment(new Date()).isBetween('2019-03-25 00:00:00', '2019-04-16 00:00:00')
    ) {
      return Timeline3
    } else if (
      moment(new Date()).isBetween('2019-04-16 00:00:00', '2019-04-27 00:00:00')
    ) {
      return Timeline4
    } else {
      return Timeline5
    }
  }
  render() {
    return (
      <div className={styles.mobileHome}>
        <div className={styles.bg}>
          <img src={bg} alt="" />
          <a
            href="https://github.com/tianrang-intelligence/TSCC2019"
            className={styles.click}
          />
        </div>
        <div className={styles.timeLine}>
          <img src={this.getTimeLineImage()} />
        </div>
        <div className={styles.trophy}>
          <img src={trophy} />
        </div>
      </div>
    )
  }
}



