import * as React from 'react'
import { observer } from 'mobx-react'
import { observable, action } from 'mobx'
import * as moment from 'moment'
import JoinModal from './HomeModals/JoinModal'
import UploadModal from './HomeModals/UploadModal'
import RankModal from './HomeModals/RankModal'
import AdaptWrapper from '@components/AdaptWrapper'
import bgImage1 from '@assets/homeImages/bg1.jpg'
import bgImage2 from '@assets/homeImages/bg2.png'
import logoImage from '@assets/homeImages/logo.png'
import enterImage from '@assets/homeImages/enter.png'
import competitionprizeImage from '@assets/homeImages/competitionprize.png'
import trophyImage from '@assets/homeImages/trophy.png'
import pointImage from '@assets/homeImages/point.png'
import companyImage from '@assets/homeImages/company.png'
import matchNameImage from '@assets/homeImages/matchName.png'
import TimelineImage1 from '@assets/homeImages/Timeline1.png'
import TimelineImage2 from '@assets/homeImages/Timeline2.png'
import TimelineImage3 from '@assets/homeImages/Timeline3.png'
import TimelineImage4 from '@assets/homeImages/Timeline4.png'
import TimelineImage5 from '@assets/homeImages/Timeline5.png'
import titleLogo from '@assets/homeImages/titleLogo.png'
import * as styles from './main.scss'
import { Button, message } from 'antd'

const logoSvg = `path("M641.5,465.5l30-11,.15-32,43.85-8-6-9v-35l-8-8v-14l9,10,5,9v-29l-6-5,18-25h-42l-6-6h-30l12,6h-29l-79-78-77,78h-30l12-6h-30l-6,6h-41l14,19,11-10,3,4-17,16v31l5-11,20-19v13l-18,17v37l-6,9,45,8v34l28,8,14,23,74,45,73-45Z")`
const trophySvg = `path("M170.5,379.5v-91l74-34,72,33v93h151v-107l88-33,86,33v107h151v-93l73-33,73,33v189l-72,32-74-32v-94h-151v109l-86,31-88-31v-109h-151v94l-73,32-73-32")`
@observer
class Main extends React.Component {
  @observable bottom = 0
  @observable left = 0
  @observable joinModalVisible = false
  @observable uploadModalVisible = false
  @observable rankModalVisible = false

  @action
  joinModalClose = () => {
    this.joinModalVisible = false
  }

  @action
  showJoinModal = () => {
    this.joinModalVisible = true
  }

  @action
  uploadModalClose = () => {
    this.uploadModalVisible = false
  }

  @action
  showUploadModal = () => {
    if(process.env.REACT_APP_GITHUB) {
      message.info("Functions are not yet open")
      return
    }
    this.uploadModalVisible = true
  }

  @action
  rankModalClose = () => {
    this.rankModalVisible = false
  }

  @action
  showRankModal = () => {
    if(process.env.REACT_APP_GITHUB) {
      message.info("Functions are not yet open")
      return
    }
    this.rankModalVisible = true
  }
  getTimeLineImage = () => {
    if( moment(new Date()).isBefore('2019-03-15 00:00:00','day')) {
      return TimelineImage1
    } else if(moment(new Date()).isBetween('2019-03-15 00:00:00', '2019-03-25 00:00:00')) {
      return TimelineImage2
    } else if(moment(new Date()).isBetween('2019-03-25 00:00:00', '2019-04-16 00:00:00')) {
      return TimelineImage3
    } else if(moment(new Date()).isBetween('2019-04-16 00:00:00', '2019-04-27 00:00:00')) {
      return TimelineImage4
    } else {
      return TimelineImage5
    }
  }

  componentDidMount() {
    window.onresize = () => {
      const dom = document.getElementById('topTitle')!
      const topTitleMinWith = dom.clientWidth
      if (topTitleMinWith < 800) {
        dom.style.minWidth = '800px'
      } else {
        dom.style.minWidth = '65%'
      }
    }
  }
  
  render() {
    return (
      <>
        <div
          style={{ display: this.rankModalVisible ? 'none' : 'flex' }}
          className={styles.topTitle}
        >
          <div id="topTitle" className={styles.wrap}>
            <div className={styles.left}>
              <div className={styles.icon}>
                <img src={titleLogo} />
              </div>
              <div className={styles.list}>
                <a target="_bank" href="https://github.com/tianrang-intelligence/TSCC2019/wiki/Traffic-Signal-Control-Competition-@2050">Rules</a>
                <a target="_bank" href="https://github.com/tianrang-intelligence/TSCC2019/tree/master/data">Data</a>
                <a target="_bank" href="https://github.com/tianrang-intelligence/TSCC2019/issues">Issue</a>
                <a onClick={this.showUploadModal}>Submisson</a>
                <a onClick={this.showRankModal}>Leaderboard</a>
              </div>
            </div>
            <div className={styles.right}>
              <Button onClick={this.showJoinModal}>Join Competition</Button>
            </div>
          </div>
        </div>
        {!this.rankModalVisible && (
          <AdaptWrapper w={1920} h={2302}>
            <div className={styles.main}>
              {this.joinModalVisible && (
                <JoinModal
                  visible={this.joinModalVisible}
                  closeCallBack={this.joinModalClose}
                />
              )}
              {this.uploadModalVisible && (
                <UploadModal
                  visible={this.uploadModalVisible}
                  closeCallBack={this.uploadModalClose}
                />
              )}

              <div className={styles.bgWrap}>
                <img className={styles.bg1} src={bgImage1} />
                <img className={styles.bg2} src={bgImage2} />
              </div>
              <div className={styles.page}>
                <div className={styles.logo}>
                  <div
                    className={styles.svgAnimation}
                    style={{
                      animationDuration: `${30}s`,
                      offsetPath: logoSvg,
                      top: '-185.8px',
                      left: '-367.3px',
                    }}
                  >
                    <img src={pointImage} />
                  </div>
                  <img src={logoImage} />
                </div>
                <div className={styles.matchName}>
                  <img src={matchNameImage} />
                </div>
                <div className={styles.enter}>
                  <a href="https://github.com/tianrang-intelligence/TSCC2019">
                    <img src={enterImage} />
                  </a>
                </div>
                <div className={styles.compony}>
                  <img src={companyImage} />
                </div>
                <div className={styles.timeline}>
                  <img src={this.getTimeLineImage()} />
                </div>
                <div className={styles.competitionprize}>
                  <img src={competitionprizeImage} />
                </div>
                <div className={styles.trophy}>
                  <div
                    className={styles.svgAnimation}
                    style={{
                      animationDuration: `${20}s`,
                      offsetPath: trophySvg,
                      top: '-234.8px',
                      left: '-127.3px',
                    }}
                  >
                    <img src={pointImage} />
                  </div>
                  <img src={trophyImage} />
                </div>
                {/* <div className={styles.footerWord}>
                  <div className={styles.top}>
                    © 2016-2018 上海天壤智能科技有限公司沪公网安备
                    31011202001484号沪ICP备16033007号
                  </div>
                  <div className={styles.bottom}>天壤智能 @版权所有</div>
                </div> */}
              </div>
            </div>
          </AdaptWrapper>
        )}
        {this.rankModalVisible ? (
          <RankModal
            visible={this.rankModalVisible}
            closeCallBack={this.rankModalClose}
          />
        ) : null}
      </>
    )
  }
}
export default Main



