import * as React from 'react'
import { Route } from 'react-router-dom'
import { observer } from 'mobx-react'
import { Router, Switch } from 'react-router-dom'

// component
import history from '@common/history'

// const
import { MAIN } from '@common/path'

import MainComponent from '@routes/Main'
import mobileComponent from '@routes/MobileComponents/Home'

function IsPC() {
  const userAgentInfo = navigator.userAgent
  const Agents = new Array(
    'Android',
    'iPhone',
    'SymbianOS',
    'Windows Phone',
    'iPad',
    'iPod'
  )
  let flag = true
  Agents.map((item: string) => {
    if (userAgentInfo.indexOf(item) > 0) {
      flag = false
    }
  })
  return flag
}

@observer
class App extends React.Component {
  render() {
    return (
      <>
        <Router history={history}>
          <Switch>
            <Route
              path={MAIN}
              component={IsPC() ? MainComponent : mobileComponent}
            />
          </Switch>
        </Router>
      </>
    )
  }
}

export default App



