import { observable, action } from "mobx";
import { hasPhoneNum, IhasPhonenum, IhasTeamName, hasTeamName, IhasEmail, hasEmail, IqueryCommit, queryCommit } from '@src/api/homePage';

class HomePage {
    @observable hasPhone:boolean = false
    @observable hasTeamName:boolean = false
    @observable hasEmail:boolean = false
    @observable commitList:IqueryCommit[] = []
    @action
    judgeHasPhone = async (params:IhasPhonenum) => {
        const res = await hasPhoneNum(params)
        if( res ) {
           this.hasPhone = res.data.data.exist
        }
    }

    @action
    judgeHasTeamName = async (params:IhasTeamName) => {
        const res = await hasTeamName(params)
        if( res ) {
           this.hasTeamName = res.data.data.exist
        }
    }

    @action
    judgeHasEmail = async (params:IhasEmail) => {
        const res = await hasEmail(params)
        if( res ) {
           this.hasEmail = res.data.data.exist
        }
    }

    @action
    getCommitList = async () => {
        const res = await queryCommit()
        if(res) {
            if(res.data) {
                this.commitList = res.data.data
            }
        }
    }
}

export default new HomePage()


