import * as React from 'react'
import * as styles from './style.scss'
import backImage from '@assets/homeImages/back.png'
import bonusTimeImage from '@assets/homeImages/bonustime.png'
import titleImage from '@assets/homeImages/leaderboard.png'
import tableImage from '@assets/homeImages/table.png'
import beforeImage from '@assets/homeImages/list.png'
import AfterImage from '@assets/homeImages/11.png'
import noDataImage from '@assets/homeImages/comingsoon.png'
import bgImage1 from '@assets/homeImages/bg1.jpg'
import bgImage2 from '@assets/homeImages/bg2.png'
import AdaptWrapper from '@components/AdaptWrapper'
import { observable, action } from 'mobx'
import * as moment from 'moment'
import { observer } from 'mobx-react'
import homePage from '@src/stores/homePage'
import { IqueryCommit, IsetResult } from '@src/api/homePage'

interface IRankModal {
  visible: boolean
  closeCallBack: () => void
}
@observer
export default class RankModal extends React.Component<IRankModal> {
  @observable days: number = 0
  @observable hours: number = 0
  @observable minutes: number = 0
  @observable seconds: number = 0

  getSetNum = (set: string, result: IsetResult[]) => {
    let data = 0
    result.map((item: IsetResult) => {
      if (item.setname === set) {
        data = item.score
      }
    })
    return data.toFixed(2)
  }

  getAverageNum = (result: IsetResult[]) => {
    let data = 0;
    result.map((item: IsetResult) => {
        data += item.score
      })
    return (data/5).toFixed(2)
  }

  getLastUpdateTime = (result: IsetResult[]) => {
      let  lastUpdateTime = moment("2000-01-01")
    result.map((item: IsetResult) => {
        lastUpdateTime.isBefore(moment(item.committime))
        lastUpdateTime = moment(item.committime)
      })
      return lastUpdateTime.format("YYYY-MM-DD")
  }

  getTableContent = (data: any) => {
    if (data.length === 0) {
      return (
        <div className={styles.noData}>
          <img src={noDataImage} alt="" />
        </div>
      )
    }
    const sets = [
      'hangzhou_bc_tyc_1h_2021',
      'hangzhou_bc_tyc_1h_7_8_1848',
      'hangzhou_bc_tyc_1h_8_9_2231',
      'hangzhou_kn_hz_1h_7_8_827',
      'hangzhou_sb_sx_1h_7_8_1671',
    ]
    return data.map((item: IqueryCommit, index: number) => {
      return (
        <div key={index} className={styles.listItem}>
          <img src={index < 10 || index === 10 ? beforeImage : AfterImage} />
          <div className={styles.itemContent}>
            <div className={styles.rank}>{index + 1}</div>
            <div className={styles.teamName}>{item.teamname}</div>
            <div className={styles.setWrap}>
              {sets.map((setItem: string) => {
                return (
                  <div key={setItem} className={styles.item}>
                    {this.getSetNum(setItem, item.result)}
                  </div>
                )

              })}
            </div>
            <div className={styles.totalScore}>{this.getAverageNum(item.result)}</div>
            <div className={styles.lastUpdate}>{this.getLastUpdateTime(item.result)}</div>
          </div>
        </div>
      )
    })
  }

  getNumber = (num: number) => {
    if (num.toString().length === 1) {
      return '0' + num
    } else {
      return num
    }
  }

  @action
  setTime = () => {
    const time = moment('2019-03-25').diff(moment())
    let t = null
    let d = null
    let h = null
    let m = null
    let s = null
    t = time / 1000
    d = Math.floor(t / (24 * 3600))
    h = Math.floor((t - 24 * 3600 * d) / 3600)
    m = Math.floor((t - 24 * 3600 * d - h * 3600) / 60)
    s = Math.floor(t - 24 * 3600 * d - h * 3600 - m * 60)
    this.days = d
    this.hours = h
    this.minutes = m
    this.seconds = s
  }

  @action
  getTimeRange = () => {
    let time = moment('2019-03-25').diff(moment())
    const timer = setInterval(() => {
      let t = null
      let d = null
      let h = null
      let m = null
      let s = null
      t = time / 1000
      d = Math.floor(t / (24 * 3600))
      h = Math.floor((t - 24 * 3600 * d) / 3600)
      m = Math.floor((t - 24 * 3600 * d - h * 3600) / 60)
      s = Math.floor(t - 24 * 3600 * d - h * 3600 - m * 60)
      this.days = d
      this.hours = h
      this.minutes = m
      this.seconds = s
      time -= 1000
      if (time < 0) {
        clearInterval(timer)
      }
    }, 1000)
  }

  componentDidMount() {
    this.setTime()
    this.getTimeRange()
    homePage.getCommitList()
  }

  render() {
    const data: IqueryCommit[] = homePage.commitList
    return (
      <div id="rankModal" className={styles.rankModal}>
        <AdaptWrapper w={1920} h={1240}>
          <div className={styles.wrap}>
            <div className={styles.bg}>
              <img src={bgImage1} />
              <img src={bgImage2} />
            </div>
            <div className={styles.content}>
              <div className={styles.back} onClick={this.props.closeCallBack}>
                <img src={backImage} />
              </div>
              <div className={styles.bounsTime}>
                <img src={bonusTimeImage} />
                <div className={styles.time}>
                  <div className={styles.days}>{this.getNumber(this.days)}</div>
                  <div className={styles.hours}>
                    {this.getNumber(this.hours)}
                  </div>
                  <div className={styles.minutes}>
                    {this.getNumber(this.minutes)}
                  </div>
                  <div className={styles.seconds}>
                    {this.getNumber(this.seconds)}
                  </div>
                </div>
              </div>
              <div className={styles.title}>
                <img src={titleImage} />
              </div>
              <div className={styles.table}>
                <img src={tableImage} />
                <div className={styles.tableContent}>
                  {this.getTableContent(data)}
                </div>
              </div>
            </div>
          </div>
        </AdaptWrapper>
      </div>
    )
  }
}



